export default {
	data() {
		return {
			globalUser: {}
		};
	},
	onShow() {
		var me = this;
		var globalUser = me.getGlobalUser("globalUser");
		me.globalUser = globalUser;
	},
	methods: {
		modifySex_yym() {
			uni.navigateTo({
				url: "../mySex/mySex"
			})
		},
		modifyBirthday_yym() {
			uni.navigateTo({
				url: "../myBirthday/myBirthday"
			})
		},
		modifyNickname_yym() {
			uni.navigateTo({
				url: "../myNickname/myNickname"
			})
		},
		modifyPassWord_yym() {
			uni.navigateTo({
				url: "../myPassword/myPassword"
			})
		},
		operator_yym() {
			var me = this;
			var globalUser = me.getGlobalUser("globalUser");
			uni.showActionSheet({
				itemList: ["查看我的头像", "从相册选择上传"],
				success(res) {
					var index = res.tapIndex;
					if (index == 0) {
						// 预览头像
						var faceArr = [];
						faceArr.push(globalUser.faceImage);
						uni.previewImage({
							urls: faceArr,
							current: faceArr[0]
						})
					} else if (index == 1) {
						// 选择上传头像
						uni.chooseImage({
							count: 1,
							sizeType: ["compressed"],
							sourceType: ["album"],
							success(res) {
								// 获得临时路径
								var tempFilePath = res.tempFilePaths[0];






								uni.navigateTo({
									url: "../faceCrop/faceCrop?tempFilePath=" + tempFilePath
								})

							}
						})
						
					}
				}
			})
		},
		cleanStorage_yym() {
			uni.clearStorageSync();
			uni.showToast({
				title: "清理缓存成功",
				mask: false,
				duration: 1500
			})
		},
		logout_yym() {
			var globalUser = this.getGlobalUser("globalUser");
			var serverUrl = this.serverUrl;
			uni.request({
				url: serverUrl + '/user/logout?userId=' + globalUser.id,
				method: "POST",
				success: (res) => {
					// 获取真实数据之前，务必判断状态是否为200
					if (res.data.status == 200) {
						// 如果服务器返回200，代表用户在服务端退出登录成功
						uni.removeStorageSync("globalUser");
						uni.switchTab({
							url: "../me/me"
						})
					}
				}
			});
		}
	}
}
